/* App.css */

/* Global Reset & Font */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f0e0ed;
}

/* Flex layout to push footer to bottom if needed later */
#root > div {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Navbar */
.navbar {
  background-color: #a15891 !important;
}

.navbar-brand {
  font-weight: bold;
  color: white !important;
}

.navbar-nav .nav-link {
  color: white !important;
}

/* Heading */
.text-center {
  text-align: center;
}

/* AddTodo Form */
.container form {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

input.form-control {
  margin-bottom: 15px;
}

/* Tasks List */
.task-container {
  padding: 20px;
  width: 50%;
  margin: auto;
}

/* Task Item */
.task-item {
  background-color: white;
  padding: 15px;
  border-radius: 6px;
  margin-bottom: 20px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.08);
}

.task-item h4 {
  margin-bottom: 5px;
}

/* Buttons */
.btn-success {
  background-color: #a15891;
  border: none;
}

.btn-danger {
  background-color: #7d1955;
  border: none;
}

